import React from 'react';
import { useState, useEffect } from "react";
import logo from './logo.svg';
import './App.css';

function App() {

  const [currentPushups, setCurrentPushups] = useState(0);
  const [maxPushups, setMaxPushups] = useState(10);

  document.addEventListener('click', tap); 

  function tap(event){
    document.removeEventListener('click', tap);
    setCurrentPushups(currentPushups + 1)
    console.log("event")
    
  }

  useEffect( ()=>{

      if (currentPushups > maxPushups){
        setMaxPushups(currentPushups)
      }
    
  },[currentPushups])

  

  return (
    <div className="App">
      <h1>Max Pushups: {maxPushups}</h1>
      <h1>Current Pushups: {currentPushups}</h1>
    </div>
  );
}

export default App;
